import React,{Component} from 'react'

class User extends Component{
    constructor(props){
        super(props)
        this.state={popup:true}
    }
    change(){
        this.setState({popup:true})
    }
    render(){
        return(
            <div>{
                (this.state.popup)
                ?<div>
                <div id='outer'>
                    <div id='inner'>
                        <button>View Orders</button>
                        <p>LogOut</p>
                    </div>
                    </div>
                    {this.props.children}
                </div>
                :(<div><div id='button'><p onClick={this.change}>User</p></div>
                {this.props.children}
                </div>)
            }
            <style jsx>
            {`p{
                font-family:sans-serif;
                font-size:1em;
            }
            #outer{
                position:fixed;
                width:100%;
                height: 100%;
                top:0;
                bottom: 0;
                left:0;
                right:0;
                background-color:rgb(0,0,0,0.2);
            }
            #inner{
                position: absolute;
                left: 30%;
                right: 30%;
                top: 25%;
                bottom: 25%;
                height: 30%;
                margin: auto;
                background: white;
               text-align:center;
            }
           #button{
               margin-top:8em;
              position:absolute;
              margin-left:110em;
              font-size:1em;
              cursor:pointer;
            }
            `}
            </style>
            </div>
        )
    }
}

export default User


